.Viewer {
  width: 100%;
  height: 100%;
}

.Viewer-return {
  cursor: pointer;
  position: absolute;
  z-index: 1;
  text-decoration: none;
  color: rgb(0, 140, 255);
  font-size: 0.95em;
  font-weight: normal;
  display: inline-block;
  margin: 15px;
  background-color: white;
  padding: 10px;
  border-radius: 100px;
}



